// ========================  ========================
// 
function deepClone(target) {
	let result;

	if (typeof target === 'object') {
		if (Array.isArray(target)) {
			result = [];
			for (let i in target) {
				result.push(deepClone(target[i]));
			}
		} else if (target === null) {
			result = null;
		} else if (target.constructor === RegExp) {
			result = target;
		} else {
			result = {};
			for (let i in target) {
				result[i] = deepClone(target[i]);
			}
		}
	} else {
		result = target;
	}
	return result;
}

// ------------  ------------
// 
function getCurrentTime() {
	let date = new Date();
	return date.getFullYear() + '' + (parseInt(date.getMonth()) + 1) + '' + date.getDate() + '';
}
//  
function getDateString(timestamp) {
	let date = new Date(timestamp);
	return date.getFullYear() + '' + (date.getMonth() + 1) + '' + date.getDate() + '';
}
//  
function getDateStringII(timestamp) {
	let date = new Date(timestamp);
	return date.getFullYear() + '' + (date.getMonth() + 1) + '';
}
//  
function getDateStringIII(timestamp) {
	let date = new Date(timestamp);
	return date.getFullYear() + '';
}
//  
function getTimestamp(dateString) {
	if (!dateString) {
		return '';
	} else {
		let dateYC = dateString.indexOf(''); //
		let dateMC = dateString.indexOf(''); //
		let dateDC = dateString.indexOf(''); //
		let dateY = dateString.slice(0, dateYC); //
		let dateM = dateString.slice(dateYC + 1, dateMC).length == 2 ? dateString.slice(dateYC + 1, dateMC) : '0' + dateString.slice(dateYC + 1, dateMC); //
		let dateD = dateString.slice(dateMC + 1, dateDC).length == 2 ? dateString.slice(dateMC + 1, dateDC) : '0' + dateString.slice(dateMC + 1, dateDC); //
		return new Date(dateY + '/' + dateM + '/' + dateD + ' 00:00:00').getTime(); //
	}
}
//  
function getTimestampII(dateString) {
	if (!dateString) {
		return '';
	} else {
		let dateYC = dateString.indexOf(''); //
		let dateMC = dateString.indexOf(''); //
		let dateY = dateString.slice(0, dateYC); //
		let dateM = dateString.slice(dateYC + 1, dateMC).length == 2 ? dateString.slice(dateYC + 1, dateMC) : '0' + dateString.slice(dateYC + 1, dateMC); //
		return new Date(dateY + '/' + dateM + '/' + '01' + ' 00:00:00').getTime(); //
	}
}
//  
function getTimestampIII(dateString) {
	if (!dateString) {
		return '';
	} else {
		let dateYC = dateString.indexOf(''); //
		let dateY = dateString.slice(0, dateYC); //
		return new Date(dateY + '/' + '01' + '/' + '01' + ' 00:00:00').getTime(); //
	}
}

export {
	deepClone,
	getCurrentTime,
	getDateString,
	getDateStringII,
	getDateStringIII,
	getTimestamp,
	getTimestampII,
	getTimestampIII
};
